<template>
  <el-row type="flex" style="min-height: 100vh" v-if="uid">
    <el-col :span="4" v-bind:class="{show: sideClassList.show,hide: sideClassList.hide}" class="side_bar">
      <el-menu
        :default-active="$route.path"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        @select="goPage"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b">
        <el-row type="flex" justify="center">
          <img style="height: 100px; width:100px; border-radius: 50% " src="../../../assets/images/bilibili.png"/>

        </el-row>

        <el-row type="flex" justify="center" style="margin-top: 25px;margin-bottom: 25px">
          <h4 style="font-size: 17px;color:#ffff;">{{group_name}}组</h4>
        </el-row>
        <el-row type="flex" justify="center" style="margin-top: 25px;margin-bottom: 25px">
          <span style="font-size: 18px;color:#ffff; font-weight: 600">你好~{{username}}</span>

        </el-row>

        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-location"></i>
            <span>纳新管理</span>
          </template>
          <el-menu-item-group>
            <template slot="title">纳新</template>
            <el-menu-item index="/tutor/home/overView">概览</el-menu-item>
            <el-menu-item   index="/tutor/home/studentList">学生列表</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-menu-item index="/tutor/home/tutorInfo">
          <i class="el-icon-menu"></i>
          <span slot="title">个人信息管理</span>
        </el-menu-item>
      </el-menu>
    </el-col>
    <el-col :span="20" style="height: 100%">
      <el-card style='height: 100%'>
        <!--        这里需要先拿到导师的uid, 然后再去请求列表页面! -->
        <transition name="slide-fade">
          <router-view v-if="uid"/>
        </transition>
      </el-card>
    </el-col>
    <el-row>
    </el-row>
  </el-row>
</template>

<script>
  import {mapActions, mapState} from 'vuex'
  import {Notification} from "element-ui";

  export default {
    name: "home",
    data() {
      return {
        dialogVisible: false,
        sideClassList: {
          show: false,
          hide: true
        }
      }
    },
    computed: {
      ...mapState({
        token: status => status.user.token,
        username: status => status.user.username,
        uid: status => status.user.uid,
        group_name: status => status.user.group,
      })
    },
    methods: {
      ...mapActions(['Get_Tutor_Base_Info']),

      goPage(path) {
        this.$router.push(path);
      },

      checkToken() {
        if (!this.token) {
          this.$router.push('/tutor/login');
          Notification.info("请先登录哦!");
          return false
        }
        return true;
      },

      init() {
        const res = this.checkToken();
        // 先确保有token
        if (!res) {
          return
        }
        //  验证token信息
        this.getUserInfo();


      },
      // 考虑到页面刷新Vuex会清空信息/ 关闭浏览器后 ,
      // 所以后端会出一个接口, 专门验证token信息, 然后返回用户信息
      // 根据本地的token去后端拿到用户信息
      getUserInfo() {
        this.Get_Tutor_Base_Info()
      },
      handleOpen(key, keyPath) {

      },
      handleClose(key, keyPath) {
      }
    },
    created() {
      this.init();
    },
    mounted() {
      this.sideClassList.show = true;
      this.sideClassList.hide = false;
    }
  }
</script>

<style scoped>

  @import "../../../assets/css/tutor-sidebar.css";

  .side_bar {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .hide {
    transform: translateX(-300px);

  }

  .show {
    transform: translateX(0px);
  }

  ul {
    height: 100%;
  }
  .slide-fade-enter-active {
    transition: all .05s ease;
  }
  .slide-fade-leave-active {
    transition: all .05s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
  }


</style>



